<template>

  <div>

    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>
    <el-col :span="13">

      <div class="grid-content bg-purple-light">

        <el-upload
                :limit="1"
                :file-list="fileList"
                style="margin-top: 100px"
                :on-change="inFileList"
                :on-remove="handleRemoveFileList"
                class="upload-demo"
                drag
                action="https://jsonplaceholder.typicode.com/posts/"
                multiple>
          <img style="height: 100%" v-if="imageUrl" :src="imageUrl" class="avatar">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">请将封面拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>

        <el-upload
                style="margin-top: 10px"
                class="upload-demo"
                ref="upload"
                action=""
                :on-change="inFiles"
                :on-remove="handleRemoveFiles"
                :file-list="files"
                :auto-upload="false">
          <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
        </el-upload>


      </div>

    </el-col>
    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>

  </div>

</template>

<script>

    export default {
        name: 'AddFile',
        data(){
            return {
                imageUrl:'',
                fileList:[],
                files:[]
            }
        },
        methods:{
            inFileList(file, fileList){
                this.imageUrl = URL.createObjectURL(fileList[0].raw);
                this.fileList = fileList;
                this.fileList[0]['url'] = this.imageUrl;
            },
            inFiles(file,fileList){
                // console.log(file);
                let fileUrl = URL.createObjectURL(fileList[0].raw);
                this.files = fileList;
                this.files[0]['url'] = fileUrl;
            },
            handleRemoveFileList(file,fileList){
                this.fileList = fileList;
            },
            handleRemoveFiles(file,fileList){
                this.files = fileList;
            },
            submitUpload(){
                let _this = this;
                this.axios.post('index/Home/uploadFile',{"files":_this.files,"fileList":_this.fileList})
                    .then(function(response){

                    }).catch(err=>{
                    console.log("上传失败")
                });
            }
        }
    }
</script>

